body{
    font-family: 'Montserrat';
}
.displayed-products{
    height: 550px;
    margin-top: 0.5%;
    margin-left: 5%;
    padding: 1% 1%;
    box-sizing: border-box;
    display: inline-flex;
    overflow: hidden;
    display: grid;
    grid-row-gap: 2px;
    grid-template-columns: repeat(3, 1fr);
    overflow-y: scroll;
    overflow-x: hidden;
}
img {
    width: 92%;
    height: 70%;
    margin-top: 2%;
    margin-bottom: 8%;
}
.one-image{
    width: 250px;
    height: 250px;
    background-color: white;
    text-align: center;
}
p{
line-height: 0.5;
}
::-webkit-scrollbar {
width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
background: #f1f1f1; 
}

/* Handle */
::-webkit-scrollbar-thumb {
background: rgb(175, 174, 174); 
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
background: #555; 
}

@media (max-width: 1000px) {
    .displayed-products{
        width: auto;
        grid-template-columns: auto;
    }
}